import VueRouter from "vue-router";

export default VueRouter;

const Home = () => import(/*webpackChunkName:"page_home"*/ "~views/Home.vue");


export function createRouter () {
    return new VueRouter({
        routes: [
            {
                name: "home",
                path: "/",
                component: Home,
                meta: {
                    title: store => store.state.Account.school, headConf: {
                        isShow: false,
                        allowBack: false,
                    }
                }
            },
            {
                name: "home",
                path: "/home",
                component: Home,
                headConf: {
                    isShow: false,
                    allowBack: false,
                },
                meta: {
                    headConf: {
                        isShow: false,
                        allowBack: false,
                    }
                }
            }
        ]
    });
}



