import Vue from "vue";

Vue.config.productionTip = false; // 将此值设置为 false ,会关闭 Vue 启动时的提示信息，推荐
Vue.config.silent = process.env.NODE_ENV === "production";

//注册全局filter
import VueFilter from "~filters/index";

Object.keys(VueFilter).forEach(item => {
    Vue.filter(item, VueFilter[item]);
});

//注册全局Mixin
import VueMixin from "~mixins/index";

VueMixin.forEach(item => Vue.mixin(item));

//注册全局directives
import VueDirectives from "~directives/index";

Object.keys(VueDirectives).forEach(item => {
    Vue.directive(item, VueDirectives[item]);
});

//解决手机端点击事件的300ms延迟问题
import FastClick from "fastclick";

FastClick.attach(document.body);

import VueRouter, {createRouter} from "./routes/index";
//import PlusManage from "~plus/index";

Vue.use(VueRouter);

import MintUI from "mint-ui";

Vue.use(MintUI);

// import { Drawer } from "vux";
