/**
 * 金额格式过滤
 */

export function moneyFormat(val, fixed) {
  fixed = parseInt(fixed, 10) || 2;

  if (!val) return parseFloat(0).toFixed(fixed);

  if (Object.prototype.toString.call(val) !== ["object Number"]) {
    val = parseFloat(val);

    if (isNaN(val)) return parseFloat(0).toFixed(fixed);
  }

  return parseFloat(val * 10 * fixed / (10 * fixed)).toFixed(fixed);
}



